/* section */
.grocery-section {
  padding: 5rem 0;
}

.grocery-section-center {
  width: 90vw;
  margin: 0 auto;
  max-width: 35rem;
  margin-top: 8rem;
}
@media screen and (min-width: 992px) {
  .grocery-section-center {
    width: 95vw;
  }
}
.grocery-main {
  min-height: 100vh;
  display: grid;
  place-items: center;
}
/*
=============== 
Grocery List
===============
*/
.grocery-section-center {
  background: var(--clr-white);
  border-radius: var(--radius);
  box-shadow: var(--light-shadow);
  transition: var(--transition);
  padding: 2rem;
}
.grocery-section-center:hover {
  box-shadow: var(--dark-shadow);
}
.grocery-alert {
  margin-bottom: 1rem;
  height: 1.25rem;
  display: grid;
  align-items: center;
  text-align: center;
  font-size: 0.7rem;
  border-radius: 0.25rem;
  letter-spacing: var(--spacing);
  text-transform: capitalize;
}
.grocery-alert-danger {
  color: #721c24;
  background: #f8d7da;
}
.grocery-alert-success {
  color: #155724;
  background: #d4edda;
}
.grocery-form h3 {
  color: var(--clr-black);
  margin-bottom: 1.5rem;
  text-align: center;
}
.grocery-form-control {
  display: flex;
  justify-content: center;
}
.grocery {
  padding: 0.25rem;
  padding-left: 1rem;
  background: var(--clr-grey-10);
  border-radius: var(--radius);
  border: 1px solid #ccc;
  font-size: 1rem;
  flex: 1 0 auto;
  color: var(--clr-grey-5);
}
.grocery::placeholder {
  font-family: var(--ff-secondary);
  color: var(--clr-grey-5);
}
.grocery-submit-btn {
  background: var(--clr-primary-8);
  border-color: transparent;
  flex: 0 0 5rem;
  display: grid;
  align-items: center;
  padding: 0.25rem;
  text-transform: capitalize;
  letter-spacing: 2px;
  border-radius: var(--radius);
  cursor: pointer;
  content: var(--clr-primary-5);
  transition: var(--transition);
  font-size: 0.85rem;
  color: var(--clr-white);
}
.grocery-submit-btn:hover {
  background: var(--clr-primary-5);
}

.grocery-container {
  margin-top: 2rem;
}

.grocery-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  transition: var(--transition);
  padding: 0.25rem 1rem;
  border-radius: var(--radius);
  text-transform: capitalize;
}
.grocery-item:hover {
  background: var(--clr-grey-1);
}
.grocery-item:hover .grocery-title {
  color: var(--clr-black);
}
.grocery-title {
  margin-bottom: 0;
  color: var(--clr-grey-7);
  letter-spacing: 2px;
  transition: var(--transition);
}

.grocery-edit-btn,
.grocery-delete-btn {
  background: transparent;
  border-color: transparent;
  cursor: pointer;
  font-size: 1rem;
  margin: 0 0.15rem;
  transition: var(--transition);
}
.grocery-edit-btn {
  color: var(--clr-green-light);
}
.grocery-edit-btn:hover {
  color: var(--clr-green-dark);
}
.grocery-delete-btn {
  color: var(--clr-red-light);
}
.grocery-delete-btn:hover {
  color: var(--clr-red-dark);
}
.grocery-clear-btn {
  text-transform: capitalize;
  width: 10rem;
  height: 1.5rem;
  display: grid;
  align-items: center;
  background: transparent;
  border-color: transparent;
  color: var(--clr-red-light);
  margin: 0 auto;
  font-size: 0.85rem;
  letter-spacing: var(--spacing);
  cursor: pointer;
  transition: var(--transition);
  margin-top: 1.25rem;
}
.grocery-clear-btn:hover {
  color: var(--clr-red-dark);
}
